
//Desktop
@media only screen and (min-width: 800px) {
  #header-mobile {
    display: none;
  }

  #nav-mobile {
    display: none;
  }

  .l-nav-mobile {
    display: none;
  }

  #large-sidebar {
    float: left;
    width: 25%;
    min-height: 100%;
    position: relative;
  }

  .l-brand {
    text-align: center;
  }

  .l-subtitle {
    margin-bottom: 20%;
  }
;

  .l-nav h1 {
    color: #ff4b2e;
    margin: 20px 0 0;
  }

  .l-nav ul {
    display: flex;
    flex-direction: column;
  }

  .l-nav-li {
    display: flex;
    flex-direction: column;
    margin: 0 0 20% 5%;
  }

  .l-nav-li > a {
    color: #ff4b2e;
    font-size: 1.4em;
    font-weight: 500;
    padding: 5px;
  }

  .l-nav-li > a:hover {
    cursor: pointer;
    background: rgba(255, 75, 46, .2);
  }

  .l-dropdown-content {
    display: flex;
    flex-direction: column;
    margin-left: 5%;
  }

  .l-dropdown-content a {
    padding: 5px;
    font-weight: 300;
    color: #3f3f3f;
    font-size: 1.2em;
  }

  .l-dropdown-content a:hover {
    cursor: pointer;
    background: rgba(255, 75, 46, .2);
  }

  .active {
    //background: rgba(255, 75, 46, .2);
  }
}

//Mobile

@media only screen and (max-width: 799px) {
  #large-sidebar {
    display: none;
  }

  .l-nav {
    display: none;
  }
}